 <!--品牌列表-->
<template>
  <div  class="cms-form-layout" v-loading="loading">
    <cms-prompt collapse='true'>
      <li>客服信息需要填写完整，不完整信息将不会被保存。</li>
    </cms-prompt>
    <section class="cms-batch"></section>
    <el-form ref="form" label-width="100px">
      <el-form-item label="售前" prop="promote.name" ref="promote_name">
        <table>
          <tr>
            <th>名称</th>
            <th>类型</th>
            <th>帐号</th>
            <th>操作</th>
          </tr>
          <tr v-for="(item,index) in salePre" :key="index">
            <td style="height:45px;">
              <el-input v-model="item.name" maxlength="14" style="width:200px;"></el-input>
            </td>
            <td>
              <el-select v-model="item.type" style="width:200px;">
                <el-option label="QQ" :value="1"></el-option>
                <el-option label="旺旺" :value="2"></el-option>
              </el-select>
            </td>
            <td>
              <el-input v-model="item.number"  maxlength="16" style="width:200px;"></el-input>
            </td>
            <td class="del-btn" style="text-align:center;">
              <el-button @click="removeSalePre(index)" v-if="index>=1" size="mini" type="danger">删除</el-button>
            </td>
          </tr>
        </table>
        <el-button type="primary" @click="addSalePre" size="mini" class="add-btn">添加</el-button>
      </el-form-item>

      <el-form-item label="售后">
        <table>
          <tr>
            <th>名称</th>
            <th>类型</th>
            <th>帐号</th>
            <th>操作</th>
          </tr>
          <tr v-for="(item,index) in saleAfter" :key="index">
            <td style="height:45px;">
              <el-input v-model="item.name"  style="width:200px;"></el-input>
            </td>
            <td>
              <el-select v-model="item.type"  style="width:200px;">
                <el-option label="QQ" :value="1"></el-option>
                <el-option label="旺旺" :value="2"></el-option>
              </el-select>
            </td>
            <td>
              <el-input v-model="item.number"  style="width:200px;"></el-input>
            </td>
            <td class="del-btn"  style="text-align:center;">
              <el-button @click="removeSaleAfter(index)" v-if="index>=1" size="mini" type="danger">删除</el-button>
            </td>
          </tr>
        </table>
        <el-button type="primary" @click="addSaleAfter" class="add-btn" size="mini">添加</el-button>
      </el-form-item>
      <el-form-item label="时长范围">
        <el-input type="textarea" v-model="workRange" maxlength="50"></el-input>
        <span class="gray">填写工作时间范围，如上午 10:00 - 下午 18:00</span>
      </el-form-item>
    </el-form>
    <el-button v-prem="'/setting/save'" type="primary" @click="saveSetting" class="save-btn" style="width:80px;">保存</el-button>
  </div>
</template>

<script>
import list from '@/mixins/list'
import { storeCustomerServiceSave, storeCustomerServiceGet } from './../../../api/customer/customer.js'
export default {
  mixins: [list],
  data() {
    return {
      labelPosition: 'top',
      salePre: [
        {
          name: '',
          type: 1,
          number: ''
        }
      ],
      saleAfter: [
        {
          name: '',
          type: 1,
          number: ''
        }
      ],
      workRange: ''
    }
  },
  methods: {
    saveSetting() {
      const data = {
        salePre: this.salePre,
        saleAfter: this.saleAfter,
        workRange: this.workRange
      }
      storeCustomerServiceSave(data).then(res => {
        if (res.code === 200) {
          this.$successMessage('添加成功')
        } else {
          this.$errorMessage(res.message)
        }
      })
    },
    removeSalePre(index) {
      this.salePre.splice(index, 1)
    },
    removeSaleAfter(index) {
      this.saleAfter.splice(index, 1)
    },
    addSalePre() {
      this.salePre.push({
        'name': this.salePre.name,
        'type': this.salePre.type,
        'number': this.salePre.number
      })
      this.salePre.name = ''
      this.salePre.type = ''
      this.salePre.number = ''
    },
    addSaleAfter() {
      this.saleAfter.push({
        'name': this.saleAfter.name,
        'type': this.saleAfter.type,
        'number': this.saleAfter.number
      })
      this.saleAfter.name = ''
      this.saleAfter.type = ''
      this.saleAfter.number = ''
    },
    getInfoStorage() {
      this.loading = true
      storeCustomerServiceGet().then(res => {
        if (res.code === 200) {
          if (res.data.salePre) {
            this.salePre = JSON.parse(res.data.salePre)
          }
          if (res.data.saleAfter) {
            this.saleAfter = JSON.parse(res.data.saleAfter)
          }
          this.workRange = res.data.workRange
          this.loading = false
        } else {
          this.loading = false
          this.$errorMessage('获取列表失败')
        }
      })
    }
  },
  created() {
    this.getInfoStorage()
  }
}
</script>
<style scoped lang="scss">
table td,table td .el-select {
  width: 200px;
  margin: 0 10px;
}
.add-btn {
  margin-top: 10px;
}
.save-btn{
  margin-left: 100px;
  margin-top: 5px;
}
</style>





